import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the LingkaranDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lingkaran-detail',
  templateUrl: 'lingkaran-detail.html',
})
export class LingkaranDetailPage {
  
  lingkaranDetail : any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  this.lingkaranDetail = navParams.get("kategori");
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LingkaranDetailPage');
  }

}



